                            </el-row>
                          </el-col>
                          <el-col v-if="!$isMobileDevice" :md="6">
                            <el-row>
                              <el-col :span="6" :offset="8">
                                <div :class="parseFloat(item.overHours+ '') < 0 ? 'red-badge':''">
                                  <el-badge :value="item.overHours " :max="99" class="item">
                                    <i class="fa fa-clock-o "></i>
