 JavaScript */\n    position: fixed;\n    /* Stay in place */\n    z-index: 1;\n    /* Stay on top */\n    top: 50;\n    /* Stay at the top */\n    right: 0;\n    background-color: white;\n    /* Black*/\n    overflow-x: hidden;\n    /* Disable horizontal scroll */\n    padding-top: 30px;\n    /* Place content 60px from the top */\n    -webkit-transition: 0.5s;\n    transition: 0.5s;\n    /* 0.5 second transition effect to slide in the sidenav */\n}\n.timeline-page .sidenav a {\n    text-decoration: none;\n    font-size: 25px;\n    color: #818181;\n    display: block;\n    -webkit-transition: 0.3s;\n    transition: 0.3s;\n}\n.timeline-page .menuTab {\n    padding-left: 5px;\n}\n.timeline-page .sidenav a:hover {\n    color: #f1f1f1;\n}\n.timeline-page .sidenav .closebtn {\n    position: absolute;\n    top: 0;\n    right: 10px;\n    font-size: 25px;\n    margin-left: 50px;\n}\n.timeline-page #main {\n    -webkit-transition: margin-left 0.5s;\n    transition: margin-left 0.5s;\n    padding: 20px;\n}\n.timeline-page #main .el-select {\n      padding-bottom: 10px;\n}\n@media screen and (max-height: 450px) {\n.timeline-page .sidenav {\n      padding-top: 15px;\n}\n.timeline-page .sidenav a {\n      font-size: 18px;\n}\n}\n@media screen and (max-width: 768px) {\n.timeline-page .el-date-range-picker {\n      overflow: auto !important;\n      width: 100% !important;\n}\n}\n.timeline-page .left-scroll {\n    max-height: 83vh;\n    overflow-y: scroll;\n}\n.timeline-page .right-scroll {\n    max-height: 54vh;\n    overflow-y: scroll;\n}\n@media screen and (max-width: 1366px) {\n.timeline-page .left-scroll {\n      max-height: 79vh;\n      overflow-y: scroll;\n}\n.timeline-page .right-scroll {\n      max-height: 49vh;\n      overflow-y: scroll;\n}\n}\n@media screen and (max-width: 1366px) and (max-width: 375px) {\n.timeline-page .left-scroll {\n    max-height: 77vh;\n    overflow-y: scroll;\n}\n}\n.timeline-page .pd-right-10 {\n    padding-right: 10px;\n}\n.timeline-page .padding-0 {\n    padding: 0 !important;\n}\n.timeline-page .b-600 {\n    font-weight: 600;\n}\n.timeline-page body {\n    font-size: 13px;\n}\n.timeline-page .span-block-border {\n    text-align: center;\n    width: 60%;\n    display: inline-block;\n}\n.timeline-page .span-block-border span {\n      margin: auto;\n}\n.timeline-page .span-block-border .el-progress-bar__outer,\n    .timeline-page .span-block-border .el-progress-bar__inner {\n      border-radius: 0px;\n}\n.timeline-page .el-progress-bar__innerText {\n    color: #0b0e0f;\n}\n.timeline-page .el-card__body {\n    padding: 5px 14px 5px 5px !important;\n}\n.timeline-page .center-div {\n    margin-left: auto;\n    margin-right: auto;\n    text-align: center;\n    width: auto;\n}\n.timeline-page .status .el-main {\n    margin-bottom: 10px;\n}\n.timeline-page .rightaligndiv {\n    padding-right: 5px;\n}\n.timeline-page .el-card {\n    margin-bottom: 5px;\n    width: 100%;\n    overflow: visible;\n}\n.timeline-page .round-image {\n    border-radius: 50%;\n    max-height: 50px;\n    display: block;\n    margin: auto;\n}\n.timeline-page .target-header {\n    font-size: 16px;\n}\n.timeline-page .el-container {\n    color: #333;\n    padding-top: 5px;\n}\n.timeline-page .el-main {\n    padding: 0px;\n}\n.timeline-page .el-main .el-container {\n      padding: 5px;\n}\n.timeline-page .el-checkbox__label {\n    margin-right: 5px;\n}\n.timeline-page .el-header {\n    padding-top: 5px;\n    height: 32px !important;\n    line-height: 27px;\n    font-size: 16px;\n    background-color: #f0f3f5;\n    border: 1px solid #c2cfd6;\n}\n.timeline-page .el-header .rightaligndiv {\n      text-align: right;\n}\n@media screen and (max-width: 599px) {\n.timeline-page .container-fluid {\n      padding: 0px 5px !important;\n}\n.timeline-page .timeline-page .el-header {\n      height: 32px !important;\n}\n}\n"],"sourceRoot":""}]);

